import { createRouter, createWebHashHistory } from 'vue-router' //引入创建路由对

import NavServer from '../components/NavServer';
import ServerItem from '../pages/ServerItem';
import RenderFarm from '../components/RenderFarm';
import RenderFarmList from '../pages/RenderFarmList';
import RenderFarmItem from '../pages/RenderFarmItem';
import WorkStation from '../components/WorkStation';
import WorkStationList from '../pages/WorkStationList';
import WorkStationItem from '../pages/WorkStationItem';
import FuNeng from '../components/FuNeng';
import UnityServe from '../components/UnityServe';


//配置信息中需要页面的相关配置
 
//创建页面：在src中创建文件夹名为views,在文件夹中创建两个页面：homeView.vue和AboutView.vue 做一些基础设计
 
//将两个页面引入进来
const routes = [//对两个页面进行配置
    {
        path:'/',
        redirect:'/NavServer'//设置默认路由
    },
    {
        path:'/NavServer',
        component:NavServer
    },
    {
        path:'/ServerItem',
        component:ServerItem
    },
    {
        path:'/RenderFarm',
        component:RenderFarm
    },
    {
        path:'/RenderFarmList',
        component:RenderFarmList
    },
    {
        path:'/RenderFarmItem',
        component:RenderFarmItem
    },
    {
        path:'/WorkStation',
        component:WorkStation
    },
    {
        path:'/WorkStationList',
        component:WorkStationList
    },
    {
        path:'/WorkStationItem',
        component:WorkStationItem
    },
    {
        path:'/UnityServe',
        component:UnityServe
    },
    {
        path:'/FuNeng',
        component:FuNeng
    },
    
]
 
const router = createRouter({//创建路由，其中为路由配置信息
    history:createWebHashHistory(),//访问方式
 
    routes
 
})
 
export default router;
